const state = {
    hasOpenDoor: false, //
    hasPopWin: false, //
    closePopWin: false, //
    phoneDoorRefresh: false, //

    hasOptionMenu: false, //
    closeOptionMenu: false, //
    propertyPreBillBackPressed: false, //
};

//  getters
let getters = {
    getOpenDoorFlag(state) {
        return state.hasOpenDoor
    },

    getPopWinFlag(state) {
        return state.hasPopWin
    },

    getClosePopWinFlag(state) {
        return state.closePopWin
    },

    getPhoneDoorRefreshFlag(state) {
        return state.phoneDoorRefresh
    },

    getOptionMenu(state) {
        return state.hasOptionMenu
    },

    getCloseOptionMenuFlag(state) {
        return state.closeOptionMenu
    }
};

const actions = {
    //
    setOpenDoorFlag(context, flag) {
        console.log("[vuex.home] setOpenDoorFlag: "+flag);
        context.commit('setOpenDoorFlag', flag);
    },

    //, true:false
    setPopWinFlag(context, flag) {
        console.log("[vuex.home] setPopWinFlag: "+flag);
        context.commit('setPopWinFlag', flag);
    },

    //true:, false:
    setClosePopWinFlag(context, flag) {
        console.log("[vuex.home] setClosePopWinFlag: "+flag);
        context.commit('setClosePopWinFlag', flag);
    },

    //true:, false:
    setPhoneDoorRefreshFlag(context, flag) {
        console.log("[vuex.home] setPhoneDoorRefreshFlag: "+flag);
        context.commit('setPhoneDoorRefreshFlag', flag);
    },

    //, true:false
    setOptionMenuFlag(context, flag) {
        console.log("[vuex.home] setOptionMenuFlag: "+flag);
        context.commit('setOptionMenuFlag', flag);
    },

    //true:, false:
    setCloseOptionMenuFlag(context, flag) {
        console.log("[vuex.home] setCloseOptionMenuFlag: "+flag);
        context.commit('setCloseOptionMenuFlag', flag);
    },

    //true:, false:
    setPropertyPreBillBackPressed(context, flag) {
        console.log("[vuex.home] setPropertyPreBillBackPressed: "+flag);
        context.commit('setPropertyPreBillBackPressed', flag);
    }
};

const mutations = {
    // 
    setOpenDoorFlag(state, flag) {
        state.hasOpenDoor = flag;
    },

    // , true:false
    setPopWinFlag(state, flag) {
        state.hasPopWin = flag;
    },

    // true:, false:
    setClosePopWinFlag(state, flag) {
        state.closePopWin = flag;
    },

    // true:, false:
    setPhoneDoorRefreshFlag(state, flag) {
        state.phoneDoorRefresh = flag;
    },

    // , true:false
    setOptionMenuFlag(state, flag) {
        state.hasOptionMenu = flag;
    },

    // true:, false:
    setCloseOptionMenuFlag(state, flag) {
        state.closeOptionMenu = flag;
    },

    //true:, false:
    setPropertyPreBillBackPressed(state, flag) {
        state.propertyPreBillBackPressed = flag;
    }
};

// 
export default {
    state,
    getters,
    actions,
    mutations
}
