//  reqDatamsgCode&phone
// loginType 0 1RSA 2: // qiao 2019.12.12
const requestLoginService = (reqData, loginType) => {
    let requestUrl = '';
    if (loginType === 0) {
        requestUrl = 'xwj-sys-user/login/loginOwnerByJhk';
    }
    else if (loginType === 1) {
        requestUrl = 'xwj-sys-user/login/loginByOwnerPasswordRSA';
    }else if (loginType === 2){
        requestUrl = 'xwj-sys-user/login/loginByOwnerPassword';
    }
    return new Promise((resolve, reject) => {
        http.get(requestUrl, reqData, 'baseUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

