<template>
  <div id="app">
    <div class="app-body">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>
    <div class="loading-view" v-show="loadingViewShow">
      <van-loading color="#17B7BD" />
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { store, storage } from "./utils/storage";
import Config from "./common/config";
import Tabbar from "./page/Home/TabBar";
import constUtils from "./common/constUtils";
import { getOwnerInfoByJhktService } from "./services/loginService";
import { Loading, Dialog, Toast } from "vant";
Vue.use(Loading, Dialog, Toast);

export default {
  name: "app",
  components: {
    Tabbar
  },
  data() {
    return {
      loadingViewShow: false,
      backButtonPressed: false //
    };
  },
  watch: {
    $route(to, from) {
      console.log(
        "curTheme: " +
          Vue.prototype.$curTheme +
          ", to.path: " +
          to.path +
          ", from.path: " +
          from.path
      );
      Vue.prototype.$fromPage = from.path;
      //
      this.$updateStb(to);
    }
  },
  computed: {
    total() {
      return this.$store.state.login.count;
    },
    shopFlag() {
      return this.$store.state.productPage.shopFlag;
    },
    flag() {
      return this.$store.state.productPage.flag;
    },
    setSpuCode() {
      return this.$store.state.productPage.setSpuCode;
    },
    setQualityCode() {
      return this.$store.state.productPage.setQualityCode;
    },
    setQualityCode() {
      return this.$store.state.productPage.setQualityCode;
    },
    setFlashSaleSpuCode() {
      return this.$store.state.productPage.setFlashSaleSpuCode;
    }
  },
  mounted() {
    this.addEventListener();
    // window.logOut = this.logOutAction;
    //app
    var paramsData = { encoder: "base64" };
    var pid = 666;
    paramsData = JSON.stringify(paramsData);
    if (this.$devicePlatform == "ios") {
      //ios
      window.webkit.messageHandlers.JSCtrl.postMessage([
        "request_userInfo_xinwojia",
        pid,
        paramsData
      ]);
    } else if (this.$devicePlatform == "android") {
      //android
      window.store.request_userInfo_xinwojia(pid, paramsData);
    }else {
      this.$router.replace("/mojingLogin");
    }
    //   store.set('goLoginRouter',()=>{
    //   this.clearTags();
    //   this.deleteAlias();
    //   console.log(this.$userInfo);
    //   storage.deleteStorage('userInfo');
    //   this.$router.replace("login");
    // })
  },
  created() {
    window.callback_userInfo_xinwojia = this.callback_userInfo_xinwojia;
    window.callback_backSignal_xinwojia = this.callback_backSignal_xinwojia;
    //
    this.initPlatformInfo();
    //
    if (this.isDeleteLocalImgFile != null) {
      console.log("app.vue" + this.isDeleteLocalImgFile);
      Vue.prototype.$deleteLacalImg(this.isDeleteLocalImgFile);
    }
    //deviceready
    document.addEventListener("deviceready", this.onDeviceReady, false);

    if (
      window.cordova &&
      window.cordova.plugins &&
      window.cordova.plugins.Keyboard
    ) {
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
      cordova.plugins.Keyboard.disableScroll(true);
    }
    //view
    store.set("showLoadingView", isShow => {
      this.loadingViewShow = isShow;
    });

    // // 
    // let userInfo = storage.getStorage("userInfo");
    // if (!userInfo.$s_code_$exp) {
    //   Vue.prototype.$setUserInfo(userInfo, false);
    // }
    let custEnv = "";
    //
    if (storage.getStorage("custUrl").$s_code_$exp) {
      custEnv = Config.urlMap[Config.defaultEnv];
      console.log(custEnv);
    } else {
      custEnv = storage.getStorage("custUrl");
    }
    storage.setStorage("custUrl", custEnv);
    store.set("custUrl", custEnv);

    //
    Vue.prototype.$curTheme = localStorage.getItem("curTheme")
      ? localStorage.getItem("curTheme")
      : "0";
    console.log("" + Vue.prototype.$curTheme);
  },

  updated() {
    if (
      this.$route.path == "/shopPage" ||
      this.$route.path == "/shopPageThemeL"
    ) {
      if (this.$stbHeight > 0) {
        //if (cordova.platformId == 'android') {
        if (this.$devicePlatform == "android" || this.$devicePlatform == "pc") {
          //padding,
          let el = document.querySelector(".van-nav-bar");
          if (el) {
            el.style.paddingTop = this.$stbHeight + "px";
            console.log(
              "Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop
            );
          }
        } else {
          //ioscss20px
        }
      } else {
        //ios20px
        let el = document.querySelector(".van-nav-bar");
        if (el) {
          el.style.paddingTop = 0;
          console.log(
            "Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop
          );
        }
      }
      return;
    }
    this.$nextTick(() => {
      if (this.$stbHeight > 0) {
        //if (cordova.platformId == 'android') {
        if (this.$devicePlatform == "android" || this.$devicePlatform == "pc") {
          //padding,
          let el = document.querySelector(".van-nav-bar");
          if (el) {
            el.style.paddingTop = this.$stbHeight + "px";
            console.log(
              "Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop
            );
          }
        } else {
          //ioscss20px
        }
      } else {
        //ios20px
        let el = document.querySelector(".van-nav-bar");
        if (el) {
          el.style.paddingTop = 0;
          console.log(
            "Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop
          );
        }
      }
    });
  },

  methods: {
    //-token:token;customerId
    bingUserInfo(paramsData) {
      let param = {
        token: paramsData.token,
        customerId: paramsData.customerId,
        accessToken: paramsData.token
      };
      getOwnerInfoByJhktService(param)
        .then(res => {
          console.log("-->" + JSON.stringify(res));
          if (res.resultCode == 0) {
            //
            this.bingSuccess(res);
          } else {
            //
            this.bingFailed(res);
          }
        }).catch(error => {
          console.log("+"+request_networkError_xinwojia);
          this.$toast("")
        });
    },
    bingSuccess(paramsData) {
      //
      let userInfo = {};
      userInfo.isGygFlag = 0; //1 0  
      userInfo.OWNER_CODE = paramsData.data.ownerCode;
      userInfo.OWNER_NAME = paramsData.data.ownerName;
      userInfo.OWNER_NICK_NAME = paramsData.data.ownerNickName;
      userInfo.PHONE = paramsData.data.phone;
      userInfo.ACCESS_TOKEN = paramsData.data.accessToken;
      // userInfo.PIC_BG_URL = respData.data.picBgUrl;
      userInfo.PIC_URL = paramsData.data.picUrl;
      //
      if (
        paramsData.data.areaInfo != undefined &&
        paramsData.data.areaInfo.length > 0
      ) {
        userInfo.isNoArea = 0; //
        //
        let areaInfo = {};
        if (this.$setVirtualArea) {
          areaInfo = this.$setUserAreaInfo;
        } else {
          areaInfo = paramsData.data.areaInfo[0];
        }
        userInfo.AREA_CODE = areaInfo.areaCode;
        userInfo.AREA_NAME = areaInfo.areaName;
        userInfo.BUILD_CODE = areaInfo.buildCode;
        userInfo.BUILD_NAME = areaInfo.buildName;
        userInfo.CITY_CODE = areaInfo.cityCode;
        userInfo.CITY_NAME = areaInfo.cityName;
        userInfo.DEPT_CODE = areaInfo.deptCode;
        userInfo.FLOOR_NO = areaInfo.floorNo;
        userInfo.ROOM_CODE = areaInfo.roomCode;
        userInfo.ROOM_NAME = areaInfo.roomName;
        userInfo.UNIT_NO = areaInfo.unitNo;
        userInfo.AREA_NO = areaInfo.areaNo; //
        userInfo.ROOM_NO = areaInfo.roomNo; //
      }
      Vue.prototype.$setUserInfo(userInfo); //
      this.turnToEnterPage(paramsData); //
    },
    bingFailed(paramsData) {
      this.$router.replace({
        name: "userLogin",
        params: { bingResult: "failed" }
      });
    },
    callback_userInfo_xinwojia(pid, paramsData) {
      console.log("========"+new Date());
      let params={};
      params =JSON.parse(paramsData);
      console.log(JSON.stringify(params));
      storage.setStorage("deviceType", params.deviceType);
      Vue.prototype.$deciceType=params.deviceType;
      paramsData=params;
      if (this.$enterPage !== "moJing") {
        this.bingUserInfo(paramsData);
        let loginInfo = {};
        loginInfo.bindResult = paramsData.bindResult; //
        loginInfo.JHK_TOKEN = paramsData.token;
        loginInfo.CUSTOMER_ID = paramsData.customerId;
        if (this.$enterPage == "goodsDetail") {
          loginInfo.SPU_CODE = paramsData.spuCode;
        } else if (this.$enterPage == "shopPage") {
          loginInfo.SHOP_CODE = paramsData.shopCode;
        }
        Vue.prototype.$setLoginInfo(loginInfo); //
        storage.setStorage("token", paramsData.token); //tokenheader
        storage.setStorage("channelId", paramsData.channelId);
      }
      // else if (this.$enterPage == "moJing") {
      //   storage.setStorage("token", paramsData.token);
      //   storage.setStorage("channelId", paramsData.channelId);
      //   // this.turnToEnterPage(paramsData); //
      // }
    },
    //
    turnToEnterPage(paramsData) {
      switch (this.$enterPage) {
        case "YouPin":
          this.$router.replace("/productPage");
          break;
        case "order":
          this.$router.replace({
            name: "myOrderList",
            params: { orderType: 0 },
            query: { enter: true }
          });
          sensors.track('HuiXiangJiaPageRequest',{
            '$url':'/myOrderList',
            '$title':'',
          })
          break;
        case "coupon":
          this.$router.push({ name: 'newCouponList', params: { isInit: true } });
          break;
        case "address":
          this.$router.replace({
            path: "/shippingAddressList",
            query: { flag: 1 }
          });
          break;
        case "cart":
          this.$router.replace("/shoppingCart?enter=true");
          sensors.track('HuiXiangJiaPageRequest',{
            '$url':'/shoppingCart',
            '$title':'',
          })
          break;
        case "collect":
          this.$router.replace("/myCollect");
          sensors.track('HuiXiangJiaPageRequest',{
            '$url':'/myCollect',
            '$title':'',
          })
          break;
        case "prizeRecord":
          this.$router.replace("/prizeRecord?enter=true");
          break;
        case "points":
          this.$router.replace("/myPoints?enter=true");
          sensors.track('HuiXiangJiaPageRequest',{
            '$url':'/myPoints',
            '$title':'',
          })
          break;
        case "afterSale":
          this.$router.replace("./afterSaleList");
          break;
        case "goodsDetail":
          this.$router.replace({
            name: "goodsDetail",
            params: { spuCode: this.$loginInfo.SPU_CODE },
            query: { enter: true }
          });
          break;
        case "shopPage":
          this.$router.replace({
            name: "shopPage",
            params: { shopCode: this.$loginInfo.SHOP_CODE },
            query: { enter: true }
          });
          break;
        case "moJing":
          this.$router.replace("/mojingLogin");
          break;
      }
    },
    logOutAction() {
      if (this.$enterPage == "moJing"){
        console.log("moJing");
      }else {
        this.clearTags();
        this.deleteAlias();
        console.log(this.$userInfo);
        storage.deleteStorage("userInfo");
        this.$router.replace("login");
      }
    }, //
    //  
    addEventListener: function() {
      window.addEventListener("native.keyboardshow", e => {
        Vue.prototype.$isShowKeyboard = true;
      });
      window.addEventListener("native.keyboardhide", e => {
        setTimeout(() => {
          Vue.prototype.$isShowKeyboard = false;
        }, 200);
      });
    },

    //
    onOpenWXShare: function(data) {
      //,
      console.log("onOpenWXShare = ");
      if (this.$isLogin) {
        if (data.type == "product") {
          //
          if (
            this.$route.path == "/goodsDetail" ||
            this.$route.path == "/goodsDetailThemeL"
          ) {
            //
            this.$store.commit("setSpuCodeChange", {
              spuCode: data.params.spuCode
            });
          } else {
            //
            if (this.$curTheme == 0) {
              this.$router.push({
                name: "goodsDetail",
                params: { spuCode: data.params.spuCode }
              });
            } else {
              this.$router.push({
                name: "goodsDetailThemeL",
                params: { spuCode: data.params.spuCode }
              });
            }
          }
        }
        if (data.type == "qualityLife") {
          //
          if (this.$route.path == "/qualityDetail") {
            //
            this.$store.commit("setQualityCodeChange", {
              qualityCode: data.params.articleNo
            });
          } else {
            //
            this.$router.push({
              name: "qualityDetail",
              params: { articleNo: data.params.articleNo, articleImg: "share" }
            });
          }
        }
        if (data.type == "activityTemplate") {
          if (this.$route.path == "/activityTemplate") {
            //
            this.$store.commit("setActivityCodeChange", {
              activityCode: data.params.templateId
            });
          } else {
            //
            this.$router.push({
              name: "activityTemplate",
              params: { templateId: data.params.templateId }
            });
          }
        }
        if (data.type == "flashSaleGoods") {
          if (
            this.$route.path == "/flashSaleGoods" ||
            this.$route.path == "/flashSaleGoodsThemeL"
          ) {
            //
            this.$store.commit("setFlashSaleSpuCodeChange", {
              flashSaleSpuCode:
                data.params.activitySpuId + "," + data.params.spuCode
            });
          } else {
            //
            if (this.$curTheme == 0) {
              this.$router.push({
                name: "flashSaleGoods",
                query: {
                  spuCode: data.params.spuCode,
                  activitySpuId: data.params.activitySpuId
                }
              });
            } else {
              this.$router.push({
                name: "flashSaleGoodsThemeL",
                query: {
                  spuCode: data.params.spuCode,
                  activitySpuId: data.params.activitySpuId
                }
              });
            }
          }
        }
      } else {
        storage.setStorage("WXShareData", data, 999);
        console.log("WXShareData = ");
      }
    },

    //
    openNotification(event) {
      try {
        let curUrl = window.location.href;
        let arrUrl = curUrl.split("#");
        let pageUrl = arrUrl[1];
        if (pageUrl == "/" || pageUrl == "") {
          //
          let that = this;
          setTimeout(() => {
            that.onOpenNotification(event);
          }, 200);
          if (this.$userInfo.AREA_CODE && this.$userInfo.AREA_CODE.length > 0) {
            this.$router.push("/homePage");
          } else {
            // 
            this.$router.push("/selectArea");
          }
        } else {
          this.onOpenNotification(event);
        }
      } catch (exception) {
        console.log("JPushPlugin:onOpenNotification" + exception);
      }
    },

    //
    onOpenNotification(event) {
      let alertContent = event.alert;
      let object = event.extras;
      console.log("flag = " + object.flag);

      //
      //
      //
      if (
        "0016" == object.flag ||
        "0008" == object.flag ||
        "0009" == object.flag
      ) {
        this.$router.push({
          name: "complaintReportingDetail",
          query: {
            complateCode: object.bizCode,
            typeFlag: 0
          }
        });
      }
      //
      else if ("0014" == object.flag) {
        this.$router.push("/findPage");
      }
      //
      else if ("0010" == object.flag) {
        this.$router.push("/propertyBillList");
      }
      //
      else if ("22" == object.flag) {
        let paramsObj = {
          UUID: object.uuid,
          NOTICE_CODE: object.noticeCode,
          DEPT_CODE: this.$userInfo.DEPT_CODE,
          ROOM_CODE: this.$userInfo.ROOM_CODE,
          OWNER_CODE: this.$userInfo.OWNER_CODE,
          AREA_CODE: this.$userInfo.AREA_CODE
        };
        this.$router.push({
          name: "noticeDetail",
          params: { noticeReq: paramsObj }
        });
      }
      //
      else if ("25" == object.flag) {
        this.$router.push("/myFamily");
      }
      //
      else if ("00001" == object.flag) {
        this.onOpenWXShare(object);
      }
      //
      else {
        console.log(
          "JPushPlugin:onOpenNotification[ " +
            object.flag +
            " ]"
        );
      }
    },

    //
    initPlatformInfo() {
      if (navigator.platform == "Win32" || navigator.platform == "MacIntel") {
        Vue.prototype.$devicePlatform = "pc";
      } else {
        if (/(Android)/i.test(navigator.userAgent)) {
          //android
          Vue.prototype.$devicePlatform = "android";
        } else {
          Vue.prototype.$devicePlatform = "ios";
        }
      }
      console.log("" + Vue.prototype.$devicePlatform);
    },

    //status bar
    initStatusBar() {
      // 
      if (this.$devicePlatform == "android") {
          Vue.prototype.$stbHeight = 0;
        //Vue.prototype.$stbHeight = 24;
        //
        // let ota = navigator.ota;
        // ota.getStbHeight(
        //   {},
        //   function(res) {
        //     console.log("initStatusBar.getStbHeight: " + res);
        //     Vue.prototype.$stbHeight = res;
        //   },
        //   function(err) {
        //     console.log("initStatusBar.getStbHeight: error.");
        //   }
        // );
        //
        // //android 
        // ota.resetAppBg(
        //   {},
        //   function() {
        //     console.log("initStatusBar.resetAppBg: success.");
        //   },
        //   function(err) {
        //     console.log("initStatusBar.resetAppBg: error.");
        //   }
        // );
      } else if (this.$devicePlatform == "ios") {
        if (constUtils.isFullScreen) {
          Vue.prototype.$stbHeight = 44;
          Vue.prototype.$footBarHeight = 34;
        } else {
          Vue.prototype.$stbHeight = 20;
          Vue.prototype.$footBarHeight = 0;
        }
      }

      console.log("initStatusBar:  -> " + this.$stbHeight);
      console.log(
        "initStatusBar:  -> " + this.$footBarHeight
      );

      if (this.$stbHeight > 0) {
        if (cordova.platformId == "android") {
          // , 
          console.log("initStatusBar: .");
          StatusBar.overlaysWebView(true);
        }

        // 
        // meta,statusBgc1
        this.$route.meta.statusBgc
          ? StatusBar.styleLightContent()
          : StatusBar.styleDefault();
      }
    },

    //
    onDeviceReady() {
      console.log("onDeviceReady: .");
      //backbutton
      document.addEventListener("backbutton", this.eventBackButton, false);

      this.$plugin.usePlugin("jPush")(
        () => {
          console.log(".");
        },
        (event) => {
          //
          this.openNotification(event);
        }
      );

      //status bar
      this.initStatusBar();
    },

    //
    callback_backSignal_xinwojia(pid, params) {
      console.log("callback_backSignal_xinwojia!!");
      let curUrl = window.location.href;
      let arrUrl = curUrl.split("#");
      let pageUrl = arrUrl[1];
      console.log(
        " " + pageUrl + "" + this.$fromPage
      );

      //
      if (this.$isShowKeyboard) {
        cordova.$plugin.Keyboard.close();
        return;
      }

      // TODO:
      if (
        this.$store.state.preview.previewId &&
        this.$store.state.preview.previewId.setIsShowPreview
      ) {
        this.$store.state.preview.previewId.setIsShowPreview(false);
        this.$store.dispatch("setPreviewId", "");
        return;
      }

      // 
      // if (this.$store.state.find.keyboardId) {
      //   this.$store.state.find.keyboardId.blur();
      //   this.$store.dispatch('setKeyboardId', '');
      //   return;
      // }

      //
      if (this.$store.state.home.hasPopWin) {
        console.log("!");
        this.$store.dispatch("setClosePopWinFlag", true);
        return;
      }

      // van-dialog
      // 1.Dialog.confirm
      // 2.v-modelthis.$store.state.home.hasPopWin
      let el = document.querySelector(".van-dialog");
      if (el) {
        console.log("Dialog" + el.style.display);
        console.log(el);
        console.log(el.firstChild.innerHTML);
        // console.log(el.message);
        if (el.style.display != "none") {
          if (
            pageUrl == "/orderPayNew" ||
            (pageUrl == "/setAddress" &&
              el.firstChild.innerHTML != "") ||
            pageUrl == "/propertyPreOrderPay" ||
            pageUrl == "/propertyPreOrderPayDetail" ||
            pageUrl == "/propertyOrderPay" ||
            pageUrl == "/propertyOrderPayDetail"
          ) {
            //
          } else {
            Dialog.close();
          }
          return;
        }
      }

      //
      if (this.$store.state.find.hasPopWin) {
        console.log("!");
        this.$store.dispatch("setFindClosePopWinFlag", true);
        return;
      }

      //
      if (pageUrl == "/orderPayNew") {
        console.log("");
        this.$store.dispatch("setEventBackButtonFlag", true);
        return;
      }
      //
      if (pageUrl == "/paySuccess") {
        console.log("");
        this.$store.dispatch("setPaysuccessEventBackButtonFlag", true);
        return;
      }
      //
      if (pageUrl == "/orderDetails") {
        console.log("");
        this.$store.dispatch("setOrderDetailEventBackButtonFlag", true);
        return;
      }
      //
      if (pageUrl == "/myOrderList"||pageUrl=="/myOrderList?flag=10&ifPayReturn=true") {
        this.$store.dispatch("setOrderListEventBackButtonFlag", true);
        return;
      }
      //
      if (pageUrl == "/goodsDetail") {
        this.$store.dispatch("setGoodsDetailEventBackButtonFlag", true);
        return;
      }
      //
      if (pageUrl == "/flashSaleGoods") {
        this.$store.dispatch("setFlashSaleEventBackButtonFlag", true);
        return;
      }
      //
      if (pageUrl == "/evaluateList") {
        this.$store.dispatch("setEvaluateListEventBackButtonFlag", true);
        return;
      }

      if (pageUrl == "/setAddress") {
        this.$store.dispatch("setAddressEventBackButtonFlag", true);
        return;
      }

      if (
        (pageUrl == "/shopPage" || pageUrl == "/shopPageThemeL") &&
        this.shopFlag == 0
      ) {
        this.$router.push("/productPage");
      } else if (pageUrl == "/propertyPreOrderPay") {
        this.$router.go(-2);
      } else if (pageUrl == "/propertyOrderList") {
        //
        if (!this.$store.state.property.orderListPopupFlag) {
          this.$store.dispatch("setOrderListPopupFlag", true);
          return;
        }
      } else if (pageUrl == "/qualityDetail") {
        //
        if (!this.$store.state.quality.qualityDetalPopupFlag) {
          this.$store.dispatch("setQualityDetalPopupFlag", true);
          return;
        }
      } else if (pageUrl === "/selectArea") {
        if (this.$fromPage === "/") {
          navigator.app.exitApp();
        } else {
          this.$router.back(-1);
        }
      } else {
        console.log("==========> ");
        if (pageUrl == "/shopPage") {
          Dialog.close();
        }
        //app
        let backResult = this.$setRouterIndex();
        if (backResult) {
          this.$router.back(-1);
        }
      }
    },
    // 
    deleteAlias() {
      this.$plugin.usePlugin("deleteAlias")(
        Number(this.random()), //sequence
        () => {
          //success callback
          console.log(": .");
        },
        () => {
          //errorCallback
          console.log(".");
        }
      );
    },
    // 
    clearTags() {
      this.$plugin.usePlugin("cleanTags")(
        Number(this.random()), //sequence
        () => {
          //success callback
          storage.setStorage("jpushTags", null, 60);
          console.log(".");
        },
        () => {
          //errorCallback
          console.log(".");
        }
      );
    },
    //
    random: function() {
      var myDate = new Date(); //
      var year = myDate.getFullYear().toString();
      var month = myDate.getMonth().toString();
      var date = myDate.getDate().toString();
      var hours = myDate.getHours().toString();
      var minutes = myDate.getMinutes().toString();
      var seconds = myDate.getSeconds().toString();
      var randomNumber =
        year +
        month +
        date +
        hours +
        minutes +
        seconds +
        Math.floor(Math.random() * 1000).toString();
      return randomNumber;
    }
  }
};
</script>

