/**
 * 
 *
 * @export
 * @param {*} key key
 * @param {*} plugins 
 * @param {string} [v='1.4.15'] 
 * @returns
 */export default function loadMap () {
    return new Promise(function (resolve, reject) {
        if (window.AMap) {
            // eslint-disable-next-line no-undef
            resolve(window.AMap)
        }else {
            let script = document.createElement('script');
            script.type = 'text/javascript';
            script.async = true;
            script.src = `https://webapi.amap.com/maps?v=1.4.15&key=529ff32129046a07fd11ce5a2054a797&plugin=AMap.Driving&callback=onCallback`
            script.onerror = reject;
            document.head.appendChild(script)
        }
        window.onCallback = function () {
            resolve(window.AMap)
        }
    })
}
