<template>
  <div style="display:flex;flex-direction:column;height:100%;" class="MyPoints">
    <van-nav-bar
      id="NaviBar"
      title=""
      left-arrow
      z-index="2"
      @click-left="onClickLeft"
      @click-right="onClickRight"
    >
      <template slot="right">
        <div >
          <van-icon name="info-o" />
          <span style="font-size:12px;margin-left:3px;color:#333333;"></span>
        </div>
      </template>
    </van-nav-bar>
<!--    <navigation-bar  @onLeftClick="onClickLeft" pageName="" :bothWidth="headerItemWidth" >-->
<!--      <template v-slot:nav-right>-->
<!--        <div class="rightTitle">-->
<!--          <div class="rightBtn" @click="onClickRight"></div>-->
<!--        </div>-->
<!--      </template>-->
<!--    </navigation-bar>-->

    <van-pull-refresh v-model="headerLoading" @refresh="onRefresh">
      <div class="TopBgView" :class="{hidePoint : totalPoints == ''}">
        <div style="text-align:center;position:relative;" :class="{hasActivity:showActivity}">
          <span
            class="pointNum"
          >{{(totalPoints == '' ? '' : (totalPoints == 0 ? '' : totalPoints))}}</span>
          <!-- view -->
          <div class="activity_logo" @click="goPointsActivity" v-if="showActivity">
            <div class="activity_rotate"></div>
            <div class="activity_pointer"></div>
            <div class="activity_name"></div>
          </div>
        </div>

        <div style="text-align:center;" v-if="willExpirePoint != 0">
          <span class="willExpirePoint" >{{willExpirePoint}}</span>
        </div>
        <div class="weekView" v-if="showSignView">
          <!-- // -->
          <div class="dayList">
            <van-row type="flex" justify="space-between">
              <van-col
                span="4"
                :class="{day : true, active:  getSignData.signFlag==0&&getSignData.signDays== index||getSignData.signFlag==1&&getSignData.signDays== index+1 }"
                v-for="(day,index) in signDays"
                :key="index"
              >{{day}}</van-col>
            </van-row>
          </div>
          <!--  +   -->
          <div class="pointView">
            <!--  -->
            <van-row type="flex" justify="space-between" class="pointLineView">
              <van-col class="point" span="4" v-for="(point,index) in points" :key="index">
                <van-image
                        class="pointImg"
                        fit="contain"
                        :src="require('../../assets/images/home/checkin/icon_checkin_tips.png')"
                />
                <span class="pointText" :class="{iosStyle:isIosDevice}">+{{point}}</span>
              </van-col>
              <!--  -->
            </van-row>
            <van-row type="flex" justify="space-between">
              <van-col span="4" v-for="i in 7" :key="i">
                <div style="position: relative;width: 100%;height: 40px;">
                  <img
                    class="signImage"
                    :src="activeIndex >= i ? require('../../assets/images/home/checkin/icon_checkin_star_Light.png') : require('../../assets/images/home/checkin/icon_checkin_star_gray.png')"
                  />
                  <div :class="{startLine:true,  activeLine : activeIndex>=i}" v-if="i != 7"></div>
                </div>
              </van-col>
            </van-row>
            <!-- text -->
            <div class="tipText">
              <span></span>
              <span class="tipDayText">{{activeIndex}}</span>
              <span></span>
            </div>
            <!--  -->
            <div style="text-align: center;">
              <van-button
                :color="isSigned ? '#666666' : '#17B7BD'"
                :disabled="isSigned"
                round
                class="signedBtn"
                @click="signAction"
              >{{isSigned ? "" : ""}}</van-button>
            </div>
          </div>
        </div>
      </div>

      <div>
        <van-list v-model="footerLoading" :finished="finished" @load="onLoad">
          <van-cell
            :title="item.content_name"
            :value="(item.point_value >0  ? '+' : '') +item.point_value+''"
            size="large"
            :label="item.create_time"
            v-for="(item,index) in list"
            :key="index"
          />
        </van-list>
      </div>
    </van-pull-refresh>
    <van-dialog
      style="border:none !important;"
      v-model="showDialog"
      title=""
      confirm-button-text=""
      confirm-button-color="#FFF"
      :message="signSuccessText"
    ></van-dialog>
  </div>
</template>

<script>
  import {MyPoints} from './MyPoints'
  export default {
    name: 'MyPoints',
    mixins:[MyPoints],
    created() {
      if (this.$deciceType=='phone'){
        require('../../assets/css/phone/Mine/MyPoints.scss')
      }else if (this.$deciceType=='portraitPad'){
        require('../../assets/css/portraitPad/Mine/MyPoints.scss')
      }
    },
  }
</script>

<style scoped >

</style>