<template>
  <div class="navBar van-nav-bar" :style="navBarStyle">
    <div class="left" v-if="isShowLeft" @click="$emit('onLeftClick')" :style="bothWidth">
      <!--  -->
      <img v-if="isShowBack" :src="backImg" alt="" srcset="">
      <!--  -->
      <slot name="nav-left"></slot>
    </div>
    <div class="center">
      <!--  -->
      <span class="title" v-if="isDefault">{{pageName}}</span>
      <!--  -->
      <slot name="nav-center"></slot>
    </div>
    <div class="right" v-if="isShowRight" :style="bothWidth">
      <!--  -->
      <slot name="nav-right"></slot>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      // 
      isDefault: {
        default: true,
        type: Boolean
      },
      // 
      bothWidth: {
        type: Object,
        default: function () {
          return {
            width: '36PX'
          }
        }
      },
      // 
      isShowRight: {
        default: true,
        type: Boolean
      },
      // 
      isShowLeft: {
        default: true,
        type: Boolean
      },
      // 
      isShowBack: {
        default: true,
        type: Boolean
      },
      // 
      leftImgType: {
        type: String,
        default: '0',
      },
      // 
      pageName: {
        default: '',
        type: String
      },
      // navBar
      navBarStyle: {
        type: Object,
        default: function () {
          return {
            backgroundColor: '#fff'
          }
        }
      }
    },
    data() {
      return {
        backImg: require('@/assets/images/common/nav_return_black.png')
      }
    },
    created() {
      if (this.$deciceType=='phone'){
        require('../../assets/css/phone/common/currency/NavigationBar.scss')
      }else if (this.$deciceType=='portraitPad'){
        require('../../assets/css/portraitPad/common/currency/NavigationBar.scss')
      }
    },
    mounted() {
      this.isLeftImg();
    },
    methods: {
      // 
      isLeftImg() {
        switch (this.leftImgType) {
          case '0':
            this.backImg = require('@/assets/images/common/nav_return_black.png');
            break;
          case '1':
            this.backImg = require('@/assets/images/common/nav_return_white.png');
            break;
        }
      }
    }
  }
</script>

<style scoped>


</style>